import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import {
  Dimmer,
  Loader,
  Grid,
  Header,
  Button,
  Segment,
  Icon,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";
import {
  XYPlot,
  XAxis,
  YAxis,
  VerticalGridLines,
  HorizontalGridLines,
  VerticalBarSeries,
  Hint,
  DiscreteColorLegend,
  GradientDefs,
} from "react-vis";
import { Contexto } from "./Contexto";
import { findRenderedDOMComponentWithTag } from "react-dom/test-utils";

function ExcelDateToJSDate(serial) {
  var utc_days = Math.floor(serial - 25568);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  var date_data = new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate(),
    hours,
    minutes,
    seconds
  );

  return date_data;
}

function FechaStringShort(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "numeric" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${ye}-${mo < 10 ? "0" + mo : mo}-${da}`;
  return fechaStr;
}

function FechaStringLong(fecha) {
  var options = {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
  };
  var hora = new Intl.DateTimeFormat("en-GB", options).format(fecha);
  var fechaStr = hora;
  return fechaStr;
}

function Home({ user }) {
  const [data, setData] = useState();
  const [hoy, setHoy] = useState();
  const [ayer, setAyer] = useState();
  const [mes, setMes] = useState();
  const [filtros, setFiltros] = useState(false);
  const [chartData, setChartData] = useState();
  const [chartMes, setChartMes] = useState();
  const [chartRubros, setChartRubros] = useState();
  const [mesAnterior, setMesAnterior] = useState();
  const [aplicable, setAplicable] = useState();
  const [prueba, setPrueba] = useState(false);
  const [saving, setSaving] = useState(false);
  const [areas, setAreas] = useState();
  const [leyendaMes, setLeyendaMes] = useState();
  const [leyenda, setLeyenda] = useState();
  const [activeIndex, setActIdx] = useState();
  const [activePeriod, setActPeriod] = useState("Esta Semana");
  const [turno, setTurno] = useState("Todos");
  const [tipoReporte, setTipoReporte] = useState("Reporte de Turno");
  //Fechas
  //fechas estan erradas hay que corregir
  const dias = Array.from({ length: 31 }, (_, index) => index + 1);
  const meses = Array.from({ length: 12 }, (_, index) => index + 1);
  const rubros = ["Seguridad", "Calidad", "Ambiental", "Bioseguridad"];
  const lista = [];
  for (var i = 0; i < rubros.length; i++) {
    lista.push({ title: rubros[i] });
  }
  let today = new Date();
  let inicioDeSemana = new Date();
  let yesterday = new Date(today);
  yesterday.setDate(yesterday.getDate() - 1);
  let wd = today.getDay();
  wd === 0 ? (wd = 7) : (wd = wd);
  //Fechas
  const [dataSemana, setDS] = useState();
  const [semana, setSemana] = useState(
    FechaStringShort(inicioDeSemana.setDate(inicioDeSemana.getDate() - wd + 1))
  );
  const turnos = ["Todos", "Turno 1", "Turno 2", "Turno 3"];
  const periodos = ["Esta Semana", "Este Mes", "Hoy", "Ayer", "Seleccionar"];
  const [periodo, setPeriodo] = useState();
  const {
    ["width"]: [width, setWidth],
  } = useContext(Contexto);
  const { breakpoint: breakpoint } = useContext(Contexto);
  const {
    ["colores"]: [colores, setColores],
  } = useContext(Contexto);
  function handleSemana(fecha) {
    var anno = fecha.substring(0, 4);
    var mes = fecha.substring(5, 7);
    var dia = fecha.substring(8, 10);
    var d = new Date(anno, mes - 1, dia);
    let weekday = d.getDay();
    weekday === 0 ? (weekday = 7) : (weekday = weekday);
    d.setDate(d.getDate() - weekday + 1);
    return FechaStringShort(d);
  }

  const sacarAreas = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[1705686078];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setAreas(datas);
  };

  const sacardata = async () => {
    var data = [];
    let datas = [];
    const creds = require("./sasso-app.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1N_nSzixyWg6OI7I7hTOQ-rfCpeln-pYV4FMtm17wIR0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[1249329599];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;

    let lastr = rows.length + 1;
    var j, i;
    //await sheet.loadCells("A1:V" + lastr.toString());
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    setColores((existingItems) => {
      return existingItems.map((x) => {
        var cumple = datas.reduce(
          (prev, next) =>
            prev +
            (next.Incumple === "N" &&
              next.Tipo === x.categoria &&
              next.Reporte === tipoReporte &&
              handleSemana(
                FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido))
              ) == semana &&
              1),
          0
        );
        var total = datas.reduce(
          (prev, next) =>
            prev +
            (next.Tipo === x.categoria &&
              next.Reporte === tipoReporte &&
              handleSemana(
                FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido))
              ) == semana &&
              1),
          0
        );
        var cumplimiento = isNaN(cumple / total)
          ? "-"
          : Math.ceil((cumple / total) * 100) + "%";
        return { ...x, cumple: cumplimiento };
      });
    });
  };

  function HandleTurnos(e) {
    var valor = e.target.id;
    let reporte = tipoReporte;
    let periodo = activePeriod;
    let t = turno;
    let numTurno = t.slice(-1);
    if (
      (valor.includes("Turno") || valor.includes("Todos")) &&
      !valor.includes("Reporte")
    ) {
      setTurno(valor);
      numTurno = valor.slice(-1);
      t = valor;
    } else if (valor.includes("Reporte")) {
      setTipoReporte(valor);
      reporte = valor;
    } else {
      setActPeriod(valor);
      periodo = valor;
    }
    setColores((existingItems) => {
      return existingItems.map((x) => {
        var cumple = data.reduce(
          (prev, next) =>
            prev +
            (next.Incumple === "N" &&
              next.Tipo === x.categoria &&
              next.Reporte === reporte &&
              (periodo === "Esta Semana"
                ? handleSemana(
                    FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido))
                  ) == semana
                : periodo === "Este Mes"
                ? FechaStringShort(
                    ExcelDateToJSDate(next.FechaRecorrido)
                  ).substring(5, 7) === FechaStringShort(today).substring(5, 7)
                : periodo === "Hoy"
                ? FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido)) ===
                  FechaStringShort(today)
                : periodo === "Ayer"
                ? FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido)) ===
                  FechaStringShort(yesterday)
                : true) &&
              (reporte.includes("Turno") && t !== "Todos"
                ? next.Turno === parseInt(numTurno)
                : true) &&
              1),
          0
        );
        var total = data.reduce(
          (prev, next) =>
            prev +
            (next.Tipo === x.categoria &&
              next.Reporte === reporte &&
              (periodo === "Esta Semana"
                ? handleSemana(
                    FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido))
                  ) == semana
                : periodo === "Este Mes"
                ? FechaStringShort(
                    ExcelDateToJSDate(next.FechaRecorrido)
                  ).substring(5, 7) === FechaStringShort(today).substring(5, 7)
                : periodo === "Hoy"
                ? FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido)) ===
                  FechaStringShort(today)
                : periodo === "Ayer"
                ? FechaStringShort(ExcelDateToJSDate(next.FechaRecorrido)) ===
                  FechaStringShort(yesterday)
                : true) &&
              (reporte.includes("Turno") && t !== "Todos"
                ? next.Turno === parseInt(numTurno)
                : true) &&
              1),
          0
        );
        var cumplimiento = isNaN(cumple / total)
          ? "-"
          : Math.ceil((cumple / total) * 100) + "%";
        return { ...x, cumple: cumplimiento };
      });
    });
  }

  useEffect(() => {
    if (!areas) {
      sacarAreas();
    }
  }, []);

  useEffect(() => {
    if (!data) {
      sacardata();
    } else {
    }
  }, []);

  return (
    <>
      <Grid textAlign="center" verticalAlign="middle">
        <Dimmer active={!data || saving} page>
          <Loader>{saving ? "Saving" : "Loading"}</Loader>
        </Dimmer>
        <Grid.Row>
          <Header>
            <Button.Group>
              <Button
                id="Reporte de Turno"
                color="red"
                inverted={tipoReporte !== "Reporte de Turno"}
                style={{ borderRadius: "15px" }}
                onClick={HandleTurnos}
              >
                Reporte de Turno
              </Button>
              <Button.Or color="red" text={"o"} />
              <Button
                id="Reporte de Recorrido"
                color="red"
                inverted={tipoReporte !== "Reporte de Recorrido"}
                style={{ borderRadius: "15px" }}
                onClick={HandleTurnos}
              >
                Reporte de Recorrido
              </Button>
            </Button.Group>
          </Header>
        </Grid.Row>
        <Grid.Row columns={4}>
          <Grid.Column>
            <Button
              size="mini"
              basic
              style={{ borderRadius: "15px" }}
              fluid
              onClick={() => {
                filtros ? setFiltros(false) : setFiltros(true);
              }}
            >
              <Icon name="arrow down"></Icon> Filtros
            </Button>
          </Grid.Column>
          <Grid.Column></Grid.Column>
          <Grid.Column></Grid.Column>
          <Grid.Column></Grid.Column>
        </Grid.Row>
        {filtros && (
          <>
            <b>Periodo</b>
            <Grid.Row>
              {periodos.map((x) => {
                return (
                  <Grid.Column width={5} style={{ padding: "2px" }}>
                    <Button
                      size="tiny"
                      color="red"
                      inverted
                      content={x}
                      id={x}
                      active={activePeriod === x}
                      fluid
                      style={{ borderRadius: "15px" }}
                      onClick={HandleTurnos}
                    />
                  </Grid.Column>
                );
              })}
            </Grid.Row>
            {tipoReporte === "Reporte de Turno" && <b>Turno</b>}
            <Grid.Row columns="equal">
              {tipoReporte === "Reporte de Turno" &&
                turnos.map((x) => {
                  return (
                    <Grid.Column>
                      <Button
                        size="small"
                        color="red"
                        inverted
                        content={x}
                        id={x}
                        active={turno === x}
                        fluid
                        style={{ borderRadius: "15px" }}
                        onClick={HandleTurnos}
                      />
                    </Grid.Column>
                  );
                })}
            </Grid.Row>
          </>
        )}
        {width <= breakpoint && data && (
          <Grid.Row columns={2}>
            {colores.map((x, idx) => {
              if (idx < 2) {
                return (
                  <Grid.Column>
                    <Segment
                      style={{
                        backgroundImage: x.gradient,
                        color: x.texto,
                      }}
                    >
                      <h1>{x.cumple}</h1>
                      <p>{x.categoria}</p>
                    </Segment>
                  </Grid.Column>
                );
              }
            })}
          </Grid.Row>
        )}
        {width <= breakpoint && data && (
          <Grid.Row columns={2}>
            {colores.map((x, idx) => {
              if (idx >= 2) {
                return (
                  <Grid.Column>
                    <Segment
                      style={{
                        backgroundImage: x.gradient,
                        color: "white",
                      }}
                    >
                      <h1>{x.cumple}</h1>
                      <p>{x.categoria}</p>
                    </Segment>
                  </Grid.Column>
                );
              }
            })}
          </Grid.Row>
        )}
        {width > breakpoint && data && (
          <Grid.Row columns={4}>
            {colores.map((x) => {
              return (
                <Grid.Column>
                  <Segment
                    style={{
                      backgroundImage: x.gradient,
                      color: x.texto,
                    }}
                  >
                    <h1>{x.cumple}</h1>
                    <p>{x.categoria}</p>
                  </Segment>
                </Grid.Column>
              );
            })}
          </Grid.Row>
        )}
      </Grid>
    </>
  );
}

export default Home;
